html{box-sizing:border-box;overflow-x:hidden;overflow-y:auto;text-size-adjust:100%}*,::before,::after{box-sizing:inherit}body{font-family:"游ゴシック体",yugothic,"游ゴシック Medium","Yu Gothic Medium","游ゴシック","Yu Gothic",sans-serif;font-size:1.6rem;line-height:1;color:#000}main{display:block}h1,h2,h3,h4,h5,h6{font-size:1em;font-weight:400}button{font:inherit;cursor:pointer;appearance:none}button,input{overflow:visible}button,select{text-transform:none}a{color:inherit;text-decoration:none}li{list-style:none}img{max-width:100%;height:auto;vertical-align:top}@media screen and (-webkit-min-device-pixel-ratio: 0){img{image-rendering:-webkit-optimize-contrast}}iframe{border:none}address{font-style:normal}h1{color:red}